<template>
  <div class="contain" v-if="info!=null">
      <mt-header :title="info.title+'简介'">
          <router-link to="/" slot="left">
            <mt-button>首页</mt-button>
          </router-link>
        </mt-header>
    <div >
      <h1 v-text="info.title" class="title"></h1>
      <img v-lazy="info.images.large" alt="" class="movieimg">
      <div class="movieInfimation">
        <p class="txtinfo">
          <span class="tit">
            上映年份:
          </span>{{info.year}}</p>
        <p class="txtinfo">
          <span class="tit">类型：</span>
          <span v-for="leixing in info.genres">
            {{leixing}}
          </span>
        </p>
        <p class="txtinfo">
          <span class="tit">导演：</span>
  
          <router-link v-for="director in info.directors" :to="'/director/'+director.id">
            {{director.name}}
          </router-link>
        </p>
        <p class="txtinfo">
          <span class="tit">
            评分：
          </span>
          {{info.rating.average}}
        </p>
        <p class="txtinfo">
          <span class="tit">简介：</span>
          {{info.summary}}</p>
      </div class="comments">
  
      <div>
  
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'index',
    data() {
      return {
        info: null
      }
    },
    created: function () {
      const id = this.$route.params.movieid;
      this.$http.get('/apis/v2/movie/subject/' + id).then(response => {
        console.log(response.data);
        this.info = response.data;

      }, response => {

      });
      
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .title {
    color: #333;
    font-size: 1.4rem;
    text-align: center;
    margin: 10px 0 20px;
  }

  .movieimg {
    display: block;
    margin: 0 auto;
    padding: 10px;
    border: 1px solid #eee;
    box-shadow: 0 0 5px #ddd;
  }

  .txtinfo {
    color: #666;
    line-height: 30px;
  }

  .txtinfo a{
    color: rgb(49, 209, 182);
    text-decoration: none; 
  }
  .txtinfo span.tit {
    color: rgb(49, 209, 182);
    line-height: 30px;
    font-size: 16px;
  }

  .movieInfimation {
    margin: 10px;
  }

</style>



