<template>
  <div class="contain">
    <!-- header -->
    <mt-header title="VUE 电影">
      <router-link to="/" slot="left">
        <mt-button>首页</mt-button>
      </router-link>
      <router-link to="/search" slot="right">
        <mt-button>搜索</mt-button>
      </router-link>
      </router-link>
    </mt-header>
    <!-- slider -->
    <keep-alive>
      <mt-swipe :show-indicators="false">
        <template v-for="item in sliderImg">
          <mt-swipe-item class="slider">

            <router-link :to="'/movie/'+item.id">
              <img v-lazy="item.images.medium" :alt="item.title">
              <span>{{item.title}}</span>
            </router-link>
          </mt-swipe-item>
        </template>
      </mt-swipe>
    </keep-alive>

    <!-- top 250 -->
    <div class="movieTop250">
      <h2>影视top250
        <span>more+</span>
      </h2>
      <keep-alive>
        <ul>
          <li v-for="item in movietop250">
            <router-link :to="'/movie/'+item.id">
              <img v-lazy="item.images.large" alt="">
              <h3>{{item.title}}</h3>
            </router-link>
          </li>
        </ul>
      </keep-alive>
    </div>
    <!-- top 250 -->
    <div class="NorthAmericanboxoffice">
      <h2>北美票房榜        
      </h2>
      <keep-alive>
        <ul>
          <li v-for="item in movietop250">
            <router-link :to="'/movie/'+item.id">
              <img v-lazy="item.images.large" alt="">
              <h3>{{item.title}}</h3>
            </router-link>
          </li>
        </ul>
      </keep-alive>

    </div>
  </div>
</template>

<script>
  export default {
    name: 'index',
    data() {
      return {
        sliderImg: [],
        movietop250: [],
        NorthAmericanboxoffice:[]
      }
    },
    created: function () {
      this.$http.get('/apis/v2/movie/in_theaters').then(response => {
        this.sliderImg = response.data.subjects;
      }, response => {
        // error callback
      });
      this.$http.get('/apis/v2/movie/top250', {
        params: {
          start: 0,
          count: 18
        }
      }).then(response => {
        console.log(response.data.subjects[0]);
        this.movietop250 = response.data.subjects;
      }, response => {
        // error callback
      });
      this.$http.get('/apis/v2/movie/us_box', {
        params: {
          start: 0,
          count: 18
        }
      }).then(response => {
        console.log(response.data.subjects[0]);
        this.NorthAmericanboxoffice = response.data.subjects;
      }, response => {
        // error callback
      })
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .mint-swipe {
    height: 150vw;
  }

  .mint-swipe img {
    width: 100%;
    height: 150vw;
  }

  .mint-swipe a span {
    width: 100%;
    height: 10vw;
    line-height: 10vw;
    display: inline-block;
    color: #fff;
    background-color: rgba(0, 0, 0, .5);
    font-weight: 700;
    position: absolute;
    left: 0;
    bottom: 0;
    text-align: center;
  }

  .movieTop250 {}

  .movieTop250 h2,
  .NorthAmericanboxoffice h2 {
    height: 40px;
    line-height: 40px;
    padding-left: 10px;
    font-size: 16px;
    background-color: rgba(255, 0, 0, .4);
    color: #fff;
  }

  .movieTop250 h2 span,
  .NorthAmericanboxoffice h2 span  {
    float: right;
    padding-right: 10px;
  }

  .movieTop250 ul,
  .NorthAmericanboxoffice ul   {
    margin: 0;
    padding: 0 10px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .movieTop250 li,
  .NorthAmericanboxoffice li{
    width: 30%;
    display: inline-block;
    text-align: center;
    margin-bottom: 10px;
  }

  .movieTop250 li img,
  .NorthAmericanboxoffice li img{
    width: 94%;
    display: inline-block;
    border: 1px solid #ddd;
    padding: 2px;
    height: 132px;
  }

  .movieTop250 li h3,
  .NorthAmericanboxoffice li h3 {
    color: #333;
    font-size: 14px;
    display: inline-block;
  }

</style>



