<template>
  <div class="contain" v-if="director!=null">
    <mt-header :title="director.name">
      <router-link to="/" slot="left">
        <mt-button>首页</mt-button>
      </router-link>
    </mt-header>
    <div class="director" >
      <h2>{{director.name}}</h2>
      <h3>英文名：{{director.name_en}}</h3>
      <p class="adress">出生地：{{director.born_place}}</p>
      <img v-lazy="director.avatars.large" alt="">
      <div class="works">
        <div class="item" v-for="item in director.works">
          <router-link :to="'/movie/'+item.subject.id">
            <h4>{{item.subject.title}}</h4>
            <p>职务：
              <span v-for="role in item.roles">
                {{role}}
              </span>
            </p>
            <img v-lazy="item.subject.images.large" alt="">
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'director',
    data() {
      return {
        director: null
      }
    },
    created: function () {
      const id = this.$route.params.directorid;
      this.$http.get('/apis/v2/movie/celebrity/' + id).then(response => {
        console.log(response.data);
        this.director = response.data;
      }, response => {
        // error callback
      })
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .director {
    margin: 10px;
  }

  .director h2 {
    color: #333;
  }

  .director h3 {
    color: #666;
    font-size: 16px;
  }

  .adress {
    font-size: 14px;
    color: #999;
  }

  .works .item {
    margin: 0 0 10px;
    box-shadow: 0 0 5px #ccc;
    padding: 10px;
  }

  .works .item a {
    display: block;
    color: #333;
    text-decoration: none;
  }

  .works .item h4 {
    position: relative;
    display: inline-block;
    height: 40px;
    line-height: 40px;
    padding-left: 10px;
    margin: 0;
  }

  .works .item h4:before {
    content: '';
    position: absolute;
    left: 0;
    top: 10px;
    display: inline-block;
    width: 4px;
    height: 20px;
    background-color: rgba(255, 0, 0, .4);
    margin: 0;

  }

  .works .item p {
    display: inline-block;
    margin: 0 0 10px 0;
    padding-left: 20px;
    font-size: 14px;
    color: #666;
  }

  .works .item img {
    margin: 0 auto;
    display: block;

  }

</style>



