<template>
  <div class="contain">
    <mt-header title="搜索电影">
      <router-link to="/" slot="left">
        <mt-button>首页</mt-button>
      </router-link>
      </router-link>
    </mt-header>
    <div class="searchBox">
      <input type="text" v-model="searchvalue">
      <button @click="search" v-show="searchvalue">搜索</button>
    </div>
    <ul class="result">
      <li v-for="item in result">
        <router-link :to="'/movie/'+item.id">

          
          <img v-lazy="item.images.small" alt="">
          <div class="right">
              <h4>{{item.title}}</h4>
              <p class="genres">
                <span v-for="classArt in item.genres">
                  {{classArt}}
                </span>
              </p>
              <p class="year">上映时间：{{item.year}}</p>
          </div>

        </router-link>

      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'index',
    data() {
      return {
        searchvalue: '',
        result: []
      }
    },
    methods: {
      search: function () {
        const word = this.searchvalue;
        if (!word) {
          alert('您没有输入任何值欧~~');
        }
        this.$http.get('/apis/v2/movie/search?q=' + word).then(response => {
          console.log(response.data.subjects);
          this.result = response.data.subjects;
        }, response => {
          // error callback
        })
      }

    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .searchBox {
    position: relative;
    padding: 0 10px;
    height: 40px;
    line-height: 40px;
    background-color: #ccc;
  }

  .searchBox input {
    box-sizing: border-box;
    height: 30px;
    line-height: 30px;
    border: none;
    display: inline-block;
    width: 100%;
    border-radius: 3px;
    padding-left: 10px;
  }

  .searchBox button {
    position: absolute;
    right: 10px;
    top: 5px;
    box-sizing: border-box;
    height: 30px;
    line-height: 30px;
    border: none;
    display: block;
    width: 50px;
    border-radius: 3px;
    padding-left: 10px;
    background-color: #ccc;
  }
.result{
  list-style: none;
  margin: 10px;
  padding: 0;
}
.result li{
 margin-bottom: 10px;
 box-shadow: 0 0 2px #ddd;
 padding: 5px;
 font-size: 0;
}
.result li a{
 display: block;
 color: #333;
}
.result li img{
 width: 40%;
}
.result li .right{
  box-sizing: border-box;
 width: 60%;
 display: inline-block;
 font-size: 14px;
 vertical-align: top;
 padding-left: 10px;
}
.result li .right h4{
 box-sizing: border-box;
 width: 100%;
 display: inline-block;
 font-size: 16px;
 vertical-align: top; 
 margin: 10px 0;
}
.result li .right p span{
 box-sizing: border-box; 
 display: inline-block;
 font-size: 14px;
 border: 1px solid #ddd;
 border-radius:4px;
 margin: 0 5px;
 padding: 0 5px;
}
.year{
  padding-left: 5px;
}
</style>



