import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Index from '@/components/pages/index'
import Movie from '@/components/pages/movieinfo'
import Director from '@/components/pages/director'
import Search from '@/components/pages/search'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    }, {
      path: '/movie/:movieid',
      name: 'movie',
      component: Movie
    }, {
      path: '/director/:directorid',
      name: 'director',
      component: Director
    }, {
      path: '/search',
      name: 'search',
      component: Search
    }
  ]
})



